
const CheckBox = ({ text, onclick }) => {
    // TODO pass values via functios with params ex parent function clah(){} child clah(12);

    function handleClick(){
        onclick(text);
    }
    return(
        <div className="checkbox">
            <input type="checkbox" defaultChecked="true" onClick={handleClick}/>
            <label>{ text }</label>
        </div>
    );
}

export default CheckBox;