import CheckBox from "./CheckBox";

const CheckSection = ({ title, checkArray, updateArray }) => {
    
    function handleCheckbox(clickedBox){
        checkArray.forEach((el, i) => {
            if(el.label === clickedBox) el.checked = !el.checked;
        })

        updateArray(checkArray);
    }

    return(
        <div className="checkContainer">
            <h1>{ title }</h1>
            <hr/>
			<div className="checkBoxGrid">
				{ checkArray.map((el, i) => <CheckBox key={i} text={el.label} onclick={handleCheckbox}/> )}
            </div>
        </div>
    );
}

export default CheckSection;